//Custom
$blue: #4A90E2;
$dark_blue: #00408B;
$grey: #E1E1E1;
$light_grey: #F2F2F2;
$medium_grey: #C8C8C8;
$dark_grey: #757575;
$yellow: #FFF2DC;
$white: #FFFFFF;

* {
  position: relative;
  box-sizing: border-box; }

::placeholder {
  color: $medium_grey; }

html,body {
  font-family: Roboto, sans-serif;
  background-color: $grey; }

.todo_container {
  max-width: 620px;
  margin: 0 auto;
  padding: 0 15px; }

header {
  background-color: $blue;
  .sort {
    font-weight: 400;
    font-size: 24px;
    color: $dark_blue;
    height: 76px;
    line-height: 76px;
    text-align: center;
    cursor: pointer;
    &:hover {
      background-color: $dark_blue;
      color: $white; } }
  .sort.active {
    color: $white;
    border-bottom: 5px solid $dark_blue; } }

.content {
  .add_todo {
    input {
      width: 100%;
      height: 65px;
      font-size: 24px;
      color: $dark_grey;
      margin: 24px 0;
      padding-left: 24px;
      background: #FFFFFF;
      border: 2px solid $medium_grey;
      border-radius: 5px;
      outline: none;
      &:focus {
        border: 2px solid $blue; } } }

  .items {
    .item {
      .titleContent {
        background-color: $light_grey;
        margin-bottom: 8px;
        padding: 32px 24px;
        border-radius: 5px;
        input[type="checkbox"] {
          display: none; }
        input[type="checkbox"] , label .checked {
          width: 24px;
          height: 24px;
          background-color: $white;
          border: none; }

        input[type="checkbox"]:checked + label .checked {
          background-color: $blue; }

        .title {
          cursor: pointer;
          .checked, span {
            display: inline-block; }
          .checked {
            vertical-align: middle;
            margin-right: 16px;
            border-radius: 2px; } } }
      .detailContent {} } } }
